@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-VariableFont_wght.ttf");
  font-style: normal;
}
.header {
  position: fixed;
  z-index: 900;
  left: 0;
  top: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  width: 320px;
  padding: 24px 32px;
  background-color: rgb(255, 255, 255);
}
@media (max-width: 1199px) {
  .header {
    right: 0;
    bottom: auto;
    width: auto;
    display: grid;
    grid-template-columns: 1fr 48px;
    padding: 8px 16px;
  }
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 160px;
  padding: 10px 24px;
  margin: 0;
  border-width: 2px;
  border-style: solid;
  border-radius: 2px;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.2s;
}
.button.l {
  font-size: 18px;
  line-height: 28px;
}
@media (max-width: 767px) {
  .button.l {
    min-width: auto;
    padding: 10px 28px;
    font-size: 16px;
    line-height: 24px;
  }
}
.button.primary {
  border-color: rgb(240, 84, 79);
  background-color: rgb(240, 84, 79);
  color: rgba(255, 255, 255, 0.92);
}
.button.primary:hover:not(:disabled) {
  border-color: rgb(144, 50, 47);
  background-color: rgb(144, 50, 47);
}
.button.primary:disabled {
  opacity: 0.48;
}
.button.secondary {
  background-color: rgb(255, 255, 255);
  border-color: rgb(240, 84, 79);
  color: rgb(41, 41, 41);
}
.button.secondary:hover:not(:disabled) {
  background-color: rgb(252, 221, 220);
}
.button.secondary:disabled {
  opacity: 0.48;
}

.accordion {
  display: flex;
  flex-direction: column;
  gap: 2px;
}
.accordion__item {
  padding: 32px;
  border-radius: 2px;
}
.accordion__item:nth-child(5n+1) {
  background-color: #feeeed;
}
.accordion__item:nth-child(5n+2) {
  background-color: #fcdddc;
}
.accordion__item:nth-child(5n+3) {
  background-color: #fbccca;
}
.accordion__item:nth-child(5n+4) {
  background-color: #f9bbb9;
}
.accordion__item:nth-child(5n+5) {
  background-color: #f8aaa7;
}
@media (max-width: 767px) {
  .accordion__item {
    padding: 24px;
  }
}
@media (max-width: 767px) {
  .accordion__item {
    padding: 16px;
  }
}
.accordion__header {
  display: flex;
  justify-content: space-between;
  gap: 32px;
  font-size: 24px;
  font-weight: 600;
  line-height: 32px;
  cursor: pointer;
  transition: all 0.2s;
}
@media (max-width: 767px) {
  .accordion__header {
    gap: 16px;
    font-size: 16px;
    line-height: 24px;
  }
}
.accordion__header-text {
  display: flex;
  align-items: baseline;
  gap: 20px;
}
@media (max-width: 767px) {
  .accordion__item-number {
    display: none;
  }
}
.accordion__header-icon-wrapper {
  align-self: center;
  flex-shrink: 0;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 2px;
  transition: all 0.2s;
}
@media (max-width: 767px) {
  .accordion__header-icon-wrapper {
    width: 40px;
    height: 40px;
  }
}
.accordion__icon {
  width: 32px;
  height: 32px;
  color: rgb(41, 41, 41);
  transition: all 0.2s;
}
@media (max-width: 767px) {
  .accordion__icon {
    width: 24px;
    height: 24px;
  }
}
.accordion__icon_minus {
  display: none;
}
.accordion__icon path {
  transition: fill 0.2s;
}

.accordion__item_show .accordion__icon_plus {
  display: none;
}
.accordion__item_show .accordion__icon_minus {
  display: block;
}

.accordion__header:hover .accordion__header-icon-wrapper {
  background-color: rgba(0, 0, 0, 0.04);
}
.accordion__header:hover .accordion__icon {
  color: rgb(240, 84, 79);
}

.accordion__content {
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-top: 20px;
  margin-right: 56px;
}
@media (max-width: 767px) {
  .accordion__content {
    margin-right: 0;
  }
}
.accordion__content ul {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding: 0;
  margin: 0;
  list-style: none;
}
.accordion__content ul li {
  position: relative;
  padding-left: 36px;
}
@media (max-width: 767px) {
  .accordion__content ul li {
    padding-left: 24px;
  }
}
.accordion__content ul li::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 4px;
  border-radius: 20px;
  background-color: rgb(240, 84, 79);
}
@media (max-width: 767px) {
  .accordion__content ul li::after {
    width: 16px;
    height: 2px;
  }
}

.accordion__item:not(.accordion__item_show) .accordion__body {
  display: none;
}

.text-input {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-input__label {
  margin-bottom: 4px;
  margin-left: 2px;
  font-size: 14px;
  line-height: 20px;
}
.text-input__input {
  min-height: 48px;
  padding: 10px 34px 10px 16px;
  border-radius: 2px;
  border: 2px solid rgba(0, 0, 0, 0.24);
  background-color: rgb(255, 255, 255);
  color: inherit;
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  transition: all 0.2s;
}
.text-input__input::placeholder {
  color: rgba(41, 41, 41, 0.56);
}
.text-input__input:hover {
  border-color: rgba(0, 0, 0, 0.24);
}
.text-input__input:focus {
  outline: none;
  border-bottom-color: rgb(21, 136, 224);
}
.text-input__input:disabled {
  border-color: rgba(0, 0, 0, 0.08);
  background-color: rgba(0, 0, 0, 0.08);
}
.text-input__input.error {
  border-color: rgb(240, 84, 79);
}
.text-input__input_force-alert {
  border-color: rgb(240, 84, 79);
}
.text-input__input-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-input__reset {
  display: none;
}
.text-input__show {
  display: flex;
}
.text-input__reset, .text-input__show {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 8px;
  width: 24px;
  height: 24px;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.text-input__reset:hover svg, .text-input__show:hover svg {
  color: rgb(41, 41, 41);
}
.text-input__reset.active, .text-input__show.active {
  display: flex;
}
.text-input__icon {
  color: rgb(41, 41, 41);
  transition: all 0.2s;
}
.text-input__alert {
  display: none;
  margin-top: 4px;
  color: rgb(240, 84, 79);
  font-size: 14px;
  line-height: 20px;
}
.text-input__alert.active {
  display: block;
}
.text-input__addon {
  margin-top: 4px;
  font-size: 14px;
  line-height: 20px;
}
.text-input__sub-text {
  margin-top: 4px;
  color: rgba(41, 41, 41, 0.8);
  font-size: 14px;
  line-height: 20px;
}
.text-input__force-alert {
  margin-top: 4px;
  color: rgb(240, 84, 79);
  font-size: 14px;
  line-height: 20px;
}

.text-area {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-area__label {
  margin-bottom: 4px;
  color: rgba(41, 41, 41, 0.8);
  font-size: 14px;
  line-height: 20px;
}
.text-area__input {
  resize: none;
  height: 128px;
  padding: 12px;
  border-radius: 2px;
  border: 2px solid rgba(0, 0, 0, 0.16);
  background: rgb(255, 255, 255);
  color: rgb(41, 41, 41);
  font-family: inherit;
  font-size: 16px;
  line-height: 24px;
  transition: all 0.2s;
}
.text-area__input::placeholder {
  color: rgba(41, 41, 41, 0.56);
}
.text-area__input:hover {
  border-color: rgba(0, 0, 0, 0.24);
}
.text-area__input:focus {
  outline: none;
  border-bottom-color: rgb(21, 136, 224);
}
.text-area__input:disabled {
  border-color: rgba(0, 0, 0, 0.08);
  background-color: rgba(0, 0, 0, 0.08);
}
.text-area__counter {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  gap: 2px;
  color: rgba(41, 41, 41, 0.8);
  font-size: 14px;
  line-height: 20px;
}
.text-area__counter_top {
  transform: translateY(-100%);
}

.select-component-wrapper {
  position: relative;
}
.select-component-wrapper .select-component {
  position: relative;
  max-width: 240px;
  padding: 0 8px;
  height: 36px;
  display: flex;
  align-items: center;
  border-radius: 8px;
  border: 1px solid #d6d6d6;
  background: #fff;
  transition: border-color 0.2s;
}
.select-component-wrapper .select-component:hover {
  border-color: rgba(1, 144, 219, 0.48);
}
.select-component-wrapper .select-component:focus {
  outline: none;
  box-shadow: none;
}
.select-component-wrapper .select-component_selected {
  background-color: rgba(1, 144, 219, 0.08);
  border-color: rgba(1, 144, 219, 0.48);
}
.select-component-wrapper .select-component_selected .select-component__label {
  color: #006fbc;
}
.select-component-wrapper .select-component__label {
  margin-bottom: 4px;
  color: #5c5c5c;
  font-size: 12px;
  line-height: 16px;
}
.select-component-wrapper .select-component__reset {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 34px;
  top: 1px;
  right: 8px;
  padding: 0;
  border: none;
  cursor: pointer;
  background-color: transparent;
}
.select-component-wrapper .select-component__reset-icon {
  width: 16px;
  height: 16px;
  fill: #8f8f8f;
}
.select-component-wrapper .select-component .ss-values {
  color: #1f1f1f;
  font-size: 14px;
  line-height: 20px;
}
.select-component-wrapper .select-component .ss-values .ss-placeholder,
.select-component-wrapper .select-component .ss-values .ss-single {
  padding: 0 4px;
  font-size: 14px;
  line-height: 20px;
}
.select-component-wrapper .ss-open-below {
  border-color: rgba(1, 144, 219, 0.48);
  border-bottom-right-radius: 8px !important;
  border-bottom-left-radius: 8px !important;
}

.ss-content {
  margin-top: 4px !important;
  border-radius: 8px;
  border-bottom-right-radius: 8px !important;
  border-bottom-left-radius: 8px !important;
  background: #fff;
  box-shadow: 0px 16px 24px 0px rgba(0, 0, 0, 0.06), 0px 2px 6px 0px rgba(0, 0, 0, 0.04), 0px 0px 1px 0px rgba(0, 0, 0, 0.04);
}
.ss-content .ss-list {
  padding: 8px;
}
.ss-content .ss-option:not(.ss-hide) {
  display: block !important;
  border-radius: 2px;
  padding: 14px 8px !important;
  color: #1f1f1f !important;
  font-size: 14px;
  line-height: 20px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.ss-content .ss-option:not(.ss-hide):hover {
  background-color: rgba(1, 144, 219, 0.08) !important;
}
.ss-content .ss-option:not(.ss-hide).ss-selected {
  background-color: rgba(1, 144, 219, 0.08) !important;
}
.ss-content .ss-list::-webkit-scrollbar {
  width: 4px;
}
.ss-content .ss-list::-webkit-scrollbar-thumb {
  border-radius: 10px;
  border: 2px solid #d6d6d6;
}
.ss-content .ss-list::-webkit-scrollbar-track {
  background-color: #fff;
  border-radius: 10px;
}

* {
  box-sizing: border-box;
}

html {
  -webkit-font-smoothing: antialiased;
  min-height: 100%;
}

body {
  font-size: 16px;
  line-height: 1.5;
  font-weight: 400;
  font-family: "Montserrat", "Helvetica", sans-serif;
  color: rgb(41, 41, 41);
  background-color: rgb(245, 245, 245);
}

.main {
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin-left: 320px;
  padding: 48px 64px;
}
@media (max-width: 1199px) {
  .main {
    margin-left: 0;
    margin-top: 72px;
    padding: 24px;
  }
}
@media (max-width: 767px) {
  .main {
    padding: 16px;
  }
}

.hidden {
  display: none !important;
}

.transparent {
  opacity: 0 !important;
}

.body-lock {
  overflow: hidden;
}